import React, { Component } from 'react';
export default class Header extends Component {
  render() {
    return (
      <React.Fragment>
              <section id="portfolio">
                <div className="row">
                  <div className="twelve columns collapsed">
                    <h1>Check Out Some of My Works.</h1>
                    {/* portfolio-wrapper */}
                    <div id="portfolio-wrapper" className="bgrid-quarters s-bgrid-thirds cf">
                      
                      <div className="columns portfolio-item">
                        <div className="item-wrap">
                          <a href="#modal-01" title>
                            <img alt="" src="images/portfolio/ppdkm.png" />
                            <div className="overlay">
                              <div className="portfolio-item-meta">
                                <h5>BPK Information System</h5>
                                <p>PHP Desktop Application</p>
                              </div>
                            </div>
                            <div className="link-icon"><i className="icon-plus" /></div>
                          </a>
                        </div>
                      </div> {/* item end */}

                      <div className="columns portfolio-item">
                        <div className="item-wrap">
                          <a href="#modal-02" title>
                            <img alt="" src="images/portfolio/weather.png" />
                            <div className="overlay">
                              <div className="portfolio-item-meta">
                                <h5>Simple Weather</h5>
                                <p>DJango Web Application</p>
                              </div>
                            </div>
                            <div className="link-icon"><i className="icon-plus" /></div>
                          </a>
                        </div>
                      </div> {/* item end */}

                      <div className="columns portfolio-item">
                        <div className="item-wrap">
                          <a href="#modal-03" title>
                            <img alt="" src="images/portfolio/office.png" />
                            <div className="overlay">
                              <div className="portfolio-item-meta">
                                <h5>Office Inventory Application</h5>
                                <p>JAVA Application</p>
                              </div>
                            </div>
                            <div className="link-icon"><i className="icon-plus" /></div>
                          </a>
                        </div>
                      </div> {/* item end */}

                      <div className="columns portfolio-item">
                        <div className="item-wrap">
                          <a href="#modal-04" title>
                            <img alt="" src="images/portfolio/wadah.png" />
                            <div className="overlay">
                              <div className="portfolio-item-meta">
                                <h5>WADAH Information System</h5>
                                <p>PHP Desktop Application</p>
                              </div>
                            </div>
                            <div className="link-icon"><i className="icon-plus" /></div>
                          </a>
                        </div>
                      </div> {/* item end */}

                    </div> {/* portfolio-wrapper end */}
                  </div> {/* twelve columns end */}
                  {/* Modal Popup
                --------------------------------------------------------------- */}
                  <div id="modal-01" className="popup-modal mfp-hide">
                    <img className="scale-with-grid" src="images/portfolio/modals/ppdkm.png" alt="" />
                    <div className="description-box">
                      <h4>BPK Information System</h4>
                      <p>A desktop application to help record and manage government service books of the staffs under the supervision of PPD Kota Marudu.</p>
                      <span className="categories"><i className="fa fa-tag" />PHP, Desktop Application, CSS</span>
                    </div>
                    <div className="link-box">
                      <a href="https://github.com/amyleena95/bpk-ppdkm">Details</a>
                      <a className="popup-modal-dismiss">Close</a>
                    </div>
                  </div>{/* modal-01 End */}

                  <div id="modal-02" className="popup-modal mfp-hide">
                    <img className="scale-withla-grid" src="images/portfolio/modals/weather.png" alt="" />
                    <div className="description-box">
                      <h4>Simple Weather</h4>
                      <p>A web application built on DJango environment using Python and weather API.</p>
                      <span className="categories"><i className="fa fa-tag" />DJango Environment, HTML, Weather API</span>
                    </div>
                    <div className="link-box">
                      <a href="https://github.com/amyleena95/simpleweatherapp">Details</a>
                      <a className="popup-modal-dismiss">Close</a>
                    </div>
                  </div>{/* modal-02 End */}

                  <div id="modal-03" className="popup-modal mfp-hide">
                    <img className="scale-with-grid" src="images/portfolio/modals/office.png" alt="" />
                    <div className="description-box">
                      <h4>Office Inventory Application</h4>
                      <p>A desktop application developed using pure JAVA.</p>
                      <span className="categories"><i className="fa fa-tag" />JAVA</span>
                    </div>
                    <div className="link-box">
                      <a href="https://github.com/amyleena95/Office-Inventory-Application">Details</a>
                      <a className="popup-modal-dismiss">Close</a>
                    </div>
                  </div>{/* modal-03 End */}

                  <div id="modal-04" className="popup-modal mfp-hide">
                    <img className="scale-with-grid" src="images/portfolio/modals/wadah.png" alt="" />
                    <div className="description-box">
                      <h4>Wadah Information System</h4>
                      <p>An information system developed mainly using PHP and MySQL.</p>
                      <span className="categories"><i className="fa fa-tag" />Desktop Application, PHP, MySQL</span>
                    </div>
                    <div className="link-box">
                      <a href="https://github.com/amyleena95/wadah">Details</a>
                      <a className="popup-modal-dismiss">Close</a>
                    </div>
                  </div>{/* modal-04 End */}

                  
                </div> {/* row End */}
              </section>
      </React.Fragment>
    );
  }
}